'use client';

import React, { useState, useRef } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

type Props = {
  open: boolean;
  onClose: () => void;
};

const VentaRegistroModal = ({ open, onClose }: Props) => {
  if (!open) return null;

  const { registrarVenta } = useVentas();
  const idRef = useRef(Date.now());

  const [nombreCliente, setNombreCliente] = useState('');
  const [correoCliente, setCorreoCliente] = useState('');
  const [telefonoCliente, setTelefonoCliente] = useState('');
  const [serviciosDisponibles, setServiciosDisponibles] = useState<{ idServicio: string; nombre: string; costo: number }[]>([]);
  const [nuevoServicio, setNuevoServicio] = useState({ nombre: '', costo: 0 });
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [error, setError] = useState('');

  const agregarServicioDisponible = () => {
    if (!nuevoServicio.nombre || nuevoServicio.costo <= 0) return;
    const nuevo = {
      idServicio: `SVC-${Date.now()}`,
      nombre: nuevoServicio.nombre,
      costo: nuevoServicio.costo,
    };
    setServiciosDisponibles((prev) => [...prev, nuevo]);
    setNuevoServicio({ nombre: '', costo: 0 });
  };

  const agregarServicioASeleccionados = (id: string) => {
    const servicio = serviciosDisponibles.find((s) => s.idServicio === id);
    if (!servicio || detalles.some((d) => d.idServicio === id)) return;
    setDetalles((prev) => [
      ...prev,
      {
        idDetalleServicio: `DET-${Date.now()}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo,
      },
    ]);
  };

  const quitarServicio = (id: string) => {
    setDetalles((prev) => prev.filter((s) => s.idServicio !== id));
  };

  const total = Math.max(0, detalles.reduce((acc, s) => acc + s.costoTotal, 0) - descuento);

  const handleSubmit = () => {
    if (!nombreCliente || detalles.length === 0) {
      setError('‚ö†Ô∏è Debes ingresar un cliente y al menos un servicio.');
      return;
    }

    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${idRef.current}`,
      cliente: {
        idCliente: `CL-${idRef.current}`,
        nombre: nombreCliente,
        correo: correoCliente,
        telefono: telefonoCliente,
      },
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString(),
    };

    registrarVenta(venta);
    onClose();
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-2xl bg-white p-6 rounded shadow-lg">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-indigo-700">‚ûï Registrar Venta</h2>
          <button onClick={onClose} className="text-red-500 hover:text-red-700 text-2xl font-bold">√ó</button>
        </div>

        {/* Cliente */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">üìá Datos del Cliente</h3>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            <input value={nombreCliente} onChange={(e) => setNombreCliente(e.target.value)} className="border rounded p-2" placeholder="Nombre" />
            <input type="email" value={correoCliente} onChange={(e) => setCorreoCliente(e.target.value)} className="border rounded p-2" placeholder="Correo" />
            <input value={telefonoCliente} onChange={(e) => setTelefonoCliente(e.target.value)} className="border rounded p-2" placeholder="Tel√©fono" />
          </div>
        </section>

        {/* Nuevo servicio */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">‚ûï Nuevo Servicio</h3>
          <div className="flex flex-col sm:flex-row gap-3">
            <input value={nuevoServicio.nombre} onChange={(e) => setNuevoServicio({ ...nuevoServicio, nombre: e.target.value })} className="flex-1 border rounded p-2" placeholder="Nombre del servicio" />
            <input type="number" value={nuevoServicio.costo} min={0} onChange={(e) => setNuevoServicio({ ...nuevoServicio, costo: Number(e.target.value) })} className="w-40 border rounded p-2" placeholder="Costo" />
            <button onClick={agregarServicioDisponible} className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded">A√±adir</button>
          </div>
        </section>

        {/* Servicios disponibles */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">üìã Servicios Disponibles</h3>
          {serviciosDisponibles.length === 0 && <p className="text-sm text-gray-500">No hay servicios a√∫n.</p>}
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            {serviciosDisponibles.map((s) => (
              <div key={s.idServicio} className="border rounded p-3 bg-gray-50 shadow-sm">
                <div className="font-semibold text-indigo-700">{s.nombre}</div>
                <div className="text-sm text-gray-600 mb-2">Costo: {formatCurrency(s.costo)}</div>
                <button onClick={() => agregarServicioASeleccionados(s.idServicio)} className="bg-green-600 hover:bg-green-700 text-white text-sm py-1 px-3 rounded">A√±adir a la venta</button>
              </div>
            ))}
          </div>
        </section>

        {/* Servicios seleccionados */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">üõí Servicios Seleccionados</h3>
          {detalles.length === 0 && <p className="text-sm text-gray-500">No has seleccionado ning√∫n servicio.</p>}
          <ul className="space-y-2">
            {detalles.map((d) => (
              <li key={d.idDetalleServicio} className="flex justify-between items-center border p-2 bg-white rounded shadow-sm">
                <span>{d.nombreServicio}</span>
                <button onClick={() => quitarServicio(d.idServicio)} className="text-sm text-red-600 hover:underline">Quitar</button>
              </li>
            ))}
          </ul>
        </section>

        {/* Total y descuento */}
        <section className="mb-4 grid grid-cols-1 sm:grid-cols-2 gap-4 items-end">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Descuento (S/.)</label>
            <input type="number" value={descuento} min={0} onChange={(e) => setDescuento(Number(e.target.value))} className="border p-2 rounded w-full" />
          </div>
          <div className="text-right text-green-700 font-bold text-lg">
            Total: {formatCurrency(total)}
          </div>
        </section>

        {/* Errores y botones */}
        {error && <p className="text-red-600 text-sm mb-3">{error}</p>}
        <div className="flex justify-end gap-3 mt-6">
          <button onClick={onClose} className="bg-gray-200 hover:bg-gray-300 text-gray-700 px-4 py-2 rounded">‚ùå Cancelar</button>
          <button onClick={handleSubmit} className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded">üíæ Registrar</button>
        </div>
      </div>
    </div>
  );
};

export default VentaRegistroModal;
