'use client';

import React, { useState } from 'react';
import { VentaServicio } from '../../../entities/venta';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';
import VentaDetalleModal from './VentaDetalleModal';
import VentaEditarModal from './VentaEditarModal';

type Props = {
  ventas?: VentaServicio[];
};

const VentasTable = ({ ventas }: Props) => {
  const [ventaSeleccionada, setVentaSeleccionada] = useState<VentaServicio | null>(null);
  const [ventaAEditar, setVentaAEditar] = useState<VentaServicio | null>(null);

  const ventasSeguras = (ventas ?? []).filter((v) => v && v.cliente);

  const handleGuardar = (ventaActualizada: VentaServicio) => {
    setVentaAEditar(null);
  };

  return (
    <>
      <div className="rounded-xl border shadow-sm overflow-auto">
        <table className="w-full text-sm table-auto">
          <thead className="bg-gray-100 text-gray-700 uppercase tracking-wide">
            <tr>
              <th className="px-4 py-3 border">ID Venta</th>
              <th className="px-4 py-3 border">Cliente</th>
              <th className="px-4 py-3 border">Correo</th>
              <th className="px-4 py-3 border">Tel√©fono</th>
              <th className="px-4 py-3 border">Fecha</th>
              <th className="px-4 py-3 border text-center">Servicios</th>
              <th className="px-4 py-3 border text-right">Total</th>
              <th className="px-4 py-3 border text-center">Acciones</th>
            </tr>
          </thead>
          <tbody>
            {ventasSeguras.length > 0 ? (
              ventasSeguras.map((venta) => (
                <tr key={venta.idVentaServicio} className="hover:bg-gray-50">
                  <td className="px-4 py-2 border">{venta.idVentaServicio}</td>
                  <td className="px-4 py-2 border">{venta.cliente?.nombre ?? '---'}</td>
                  <td className="px-4 py-2 border">{venta.cliente?.correo ?? '---'}</td>
                  <td className="px-4 py-2 border">{venta.cliente?.telefono ?? '---'}</td>
                  <td className="px-4 py-2 border">{formatDate(venta.fechaRegistro)}</td>
                  <td className="px-4 py-2 border text-center">
                    <span className="inline-block bg-blue-100 text-blue-800 px-2 py-1 text-xs rounded-full">
                      {venta.detalles?.length ?? 0}
                    </span>
                  </td>
                  <td className="px-4 py-2 border text-right">{formatCurrency(venta.costoTotal)}</td>
                  <td className="px-4 py-2 border text-center">
                    <div className="flex justify-center gap-2">
                      <button
                        title="Ver detalles"
                        onClick={() => setVentaSeleccionada(venta)}
                        className="text-indigo-600 hover:text-indigo-800 text-lg"
                      >
                        üëÅÔ∏è
                      </button>
                      <button
                        title="Editar venta"
                        onClick={() => setVentaAEditar(venta)}
                        className="text-yellow-600 hover:text-yellow-800 text-lg"
                      >
                        ‚úèÔ∏è
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={8} className="text-center text-gray-500 py-6">
                  No se encontraron ventas.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {ventaSeleccionada && (
        <VentaDetalleModal
          venta={ventaSeleccionada}
          onClose={() => setVentaSeleccionada(null)}
        />
      )}

      <VentaEditarModal
        open={!!ventaAEditar}
        venta={ventaAEditar}
        onClose={() => setVentaAEditar(null)}
        onGuardar={handleGuardar}
      />
    </>
  );
};

export default VentasTable;
