'use client';
import React from 'react';
import { VentaServicio } from '../../../entities/venta';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';

type Props = {
  venta: VentaServicio;
  onClose: () => void;
};

const VentaDetalleModal = ({ venta, onClose }: Props) => {
  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-xl">
        <button className="modal-close-btn" onClick={onClose}>
          ‚úñ
        </button>
        <h2 className="text-xl font-bold text-indigo-700 mb-4">üìã Detalle de Venta</h2>

        <div className="space-y-2 mb-5">
          <p><strong>ID Venta:</strong> {venta.idVentaServicio}</p>
          <p><strong>Cliente:</strong> {venta.cliente.nombre}</p>
          <p><strong>Tel√©fono:</strong> {venta.cliente.telefono}</p>
          <p><strong>Correo:</strong> {venta.cliente.correo}</p>
          <p><strong>Fecha:</strong> {formatDate(venta.fechaRegistro)}</p>
        </div>

        <div className="mb-6">
          <h3 className="font-semibold mb-2">üõ† Servicios Incluidos:</h3>
          <ul className="space-y-2">
            {venta.detalles.map((detalle) => (
              <li key={detalle.idDetalleServicio} className="p-3 border rounded-lg bg-gray-50">
                <p className="font-medium">{detalle.nombreServicio}</p>
                <p className="text-sm text-gray-500">Costo: {formatCurrency(detalle.costoTotal)}</p>
              </li>
            ))}
          </ul>
        </div>

        <div className="flex justify-between border-t pt-4">
          <p className="text-sm">
            <span className="text-gray-700">Descuento aplicado: </span>
            <span className="text-red-500">{formatCurrency(venta.descuento)}</span>
          </p>
          <p className="font-bold text-green-700 text-lg">
            Total: {formatCurrency(venta.costoTotal)}
          </p>
        </div>

        <div className="flex justify-end mt-6">
          <button
            onClick={onClose}
            className="bg-indigo-600 hover:bg-indigo-700 text-white px-5 py-2 rounded-md transition"
          >
            Cerrar
          </button>
        </div>
      </div>
    </div>
  );
  
};

export default VentaDetalleModal;
