'use client';
import React from 'react';

type Filtros = {
  cliente: string;
  fechaInicio: string;
  fechaFin: string;
};

type Props = {
  filtros: Filtros;
  onFiltroChange: (filtros: Filtros) => void;
};

const VentasFiltro = ({ filtros, onFiltroChange }: Props) => {
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    onFiltroChange({ ...filtros, [e.target.name]: e.target.value });
  };

  return (
    <div className="bg-white rounded-md shadow p-4 mb-6 grid grid-cols-1 sm:grid-cols-3 gap-4">
      <input
        type="text"
        name="cliente"
        placeholder="Buscar por cliente"
        value={filtros.cliente}
        onChange={handleChange}
        className="border rounded p-2 w-full"
      />
      <input
        type="date"
        name="fechaInicio"
        value={filtros.fechaInicio}
        onChange={handleChange}
        className="border rounded p-2 w-full"
      />
      <input
        type="date"
        name="fechaFin"
        value={filtros.fechaFin}
        onChange={handleChange}
        className="border rounded p-2 w-full"
      />
    </div>
  );
};

export default VentasFiltro;
